.home {

  .homeContent {
    background: linear-gradient(115deg, $color-1 50%, transparent 100%);
    min-height: $minHeight;
    width: 100%;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;



    .content {
      // position: relative;
      // top: 5%;
      // left: 13%;
      padding: 5% 10% ;
      font-family: $font-1;


      h1 {
        font-size: 5rem;
        font-family: $font-3;
        color: $color-3;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $black;
      }
      h2 {
        font-size: 2.5rem;
        padding: 1.5rem 0 0.5rem;
        color: $color-3;
        // text-shadow: 1px 4px 1px $black;
      }
      h3 {
        font-size: 2rem;
        padding: 1.5rem 0 1.5rem;
        color: $color-5;
        // text-shadow: 1px 4px 1px $black;
      }

      .abstract {
        padding-bottom: 5rem;

      }

      p {
        padding: 1.5rem 0 0.5rem;
        color: white;
        font-size: 1.2rem;
      }
      .pdf {
        a {
          font-size: 1.5rem;
          background: $color-5;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px $color-4;
          transition: 0.2s ease;

          &:hover {
            background: $color-4;
            box-shadow: 0 5px 1px $color-5;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
  }
}
@media screen and (max-width:580px){
  .homeContent {
    text-align: center;
    padding : 10% 0;

    h1 {
      font-size: 3rem !important;
    }
    h2 {
      font-size: 1.8rem !important;
    }
    h3 {
      font-size: 1rem !important;
    }
  }
}