@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100;400;700&family=Nova+Round&family=Poppins:wght@100;400;700&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;
$font-4: 'Poppins', sans-serif;

$color-1: LightGray;
$color-2: NavajoWhite;
$color-3: Gray;
$color-4: Goldenrod;
$color-5: Peru;
$white: white;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

/*sorte de fonction réutilisable avec @include verticalCenter */
@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center; 
  }

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  body {
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font-4;
    color: $black;
  }
  button {
      /*pour avoir la petite main sur les boutons cliquables*/
    cursor: pointer;
  }
  li {
    list-style-type: none;
  }
  a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
  }
  /* Pour que les liens ne se mettent pas en violet une fois visités*/
  a:visited {
    color:$black;
  }
  .home, .knowledges, .portfolio, .contact {
    display: flex;
  
    @media screen and (max-width: 850px){
        /*Pour faire passer le menu en haut quand on réduit la taille de l'écran*/
      display: block !important;
    }
  }

  /*La on cible que la partie de droite. Sans le menu qui est inclu dans .home .contact etc*/
  .homeContent, .knowledgesContent, .portfolioContent, .contactContent {
    margin-left: .4rem;
  
    @media screen and (max-width: 850px){
      margin-left: 0 !important;
    }
  }