
/*Ici on va tout nester dans sidebar. Ainsi, on pourra réutiliser des classes dans d'autres pages, dans d'autres components, sans que ça touche ce qui se passe ici. Sass permet ça et c'est très pratique*/
.sidebar {
    background: $color-1;
    font-family: $font-1;
    /*hauteur de 100% de l'élément parent*/
    height: 100%;
    min-height: $minHeight;
    min-width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    /*1fr veut dire 1 fraction car une seule colonne*/
    grid-template-columns: 1fr;
    /*Ici, la section du milieu adaptera sa taille en fonction des 2 autres dont la taille est fixée*/
    grid-template-rows: 250px 1fr 108px;
    /*On définit les zones*/
    grid-template-areas: 
      "1"
      "2"
      "3";
  /*Puis on les affecte*/
    .id {
      grid-area: 1;
      text-align: center;
      /*Pour ne pas pouvoir cliquer dessus*/
      user-select: none;
      position: relative;
  
      .idContent {
        position: absolute;
        bottom: 0;
        left: 50%;
        /*Pour centrer l'élément*/
        transform: translateX(-50%);
        width: 100%;
  
        img {
          // border-radius: 50%;
          clip-path:circle(50% at 50% 50%);
          // box-shadow: 0 0 0 4px $color-3, 0 0 0 8px $color-4, 0 0 0 12px $color-2;
          width: 50%;
        }
        h3 {
          font-size: 1.3rem;
          text-transform: uppercase;
          letter-spacing: .2rem;
          margin-top: 1.5rem;
          color: $color-5;
        }
      }
    }
    .navigation {
      grid-area: 2;
      padding: 2rem 1rem;
  
      ul {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        
      
        li {
          padding: 1rem 0 1rem .4rem;
          font-size: 1.3rem;
          position: relative;
  
          &:hover i {
              /*Pour faire un petit effet sympa au survol des liens. Ici on dit qu'on agit sur les i (les icônes) au survol des li*/
            transform: translate(3px, 3px);
          }
  
          a {
          color: $color-5;
          }
          .navActive {
            color: $color-4;
          }
  
          i {
            position: absolute;
            /*On les réduit très légèrement*/
            font-size: 0.95rem;
            /*Pour les recaler comme il faut*/
            transform: translateY(3px);
            transition: .2s ease;
          }
          span {
            margin-left: 2.7rem;
          }
        }
      }
    }
    .socialNetwork {
      grid-area: 3;
  
      ul {
        display: flex;
        width: 264px;
        padding: 0 14px;
        justify-content: space-evenly;
  
        li {
          font-size: 1.5rem;
          background: $color-5;
          color: $color-1;
          border-radius: 50%;
          /*Pour faire des cercles plus jolis*/
          padding: .5rem 0.8rem;
          transition: .3s;
          cursor: pointer;
  
          a:visited {
            color: $color-1;
          }
          /*On a remarqué que le premier cercle n'est pas assez "écarté" sur les côtés, à cause de son logo donc on fait un petit correctif*/
          &:nth-child(1){
            padding: .5rem .85rem
          }
          /*On a remarqué que le 4eme cercle est trop "écarté" sur les côtés à cause de son logo donc on fait un petit correctif*/
          &:nth-child(4){
            padding: .5rem .75rem
          }
          &:hover {
            background: $color-4;
          }
        }
      }
      .signature {
        margin-top: 2rem;
        text-align: center;
        letter-spacing: 0.1rem;
        font-size: .7rem;
  
        p {
          color: $color-5;
  
          // &:hover {
          //   color: $color-4;
          // }
        }
      }
    }
  }

  /*On veut que la navigation se transforme pour se mettre en haut de l'écran avec disparition de la partie id (photo et nom) et réseaux sociaux*/
  @media screen and (max-width: 850px){
    .sidebar {
      display: flex;
      min-height: 50px;
      margin-bottom: .2rem;
  
      .id, .socialNetwork {
        display: none;
      }
      .navigation {
        width: 100%;
        padding: .1rem 1rem;
  
        ul {
          flex-direction: row;
          justify-content: space-between;
  
          span {
            margin-left: 1.5rem !important;
          }
        }
      }
    }
  }

  /*On veut que seuls les icônes persistent*/
  @media screen and (max-width:580px){
    .sidebar {
      ul {
        justify-content: space-around !important;
  
        li {
          padding: 1rem 0 !important;
        }
        i {
          font-size: 1.4rem !important;
          transform: translateY(-3px) !important;
        }
        span {
          display: none;
        }
      }
    }
  }