.knowledges {
  .knowledgesContent {
    min-height: $minHeight;
    width: 100%;
    position: relative;
    /*On ne veut pas voir tout ce qui dépasse*/
    overflow: hidden;
    display: grid;
    /*La fraction (fr) prend l'espace disponible. 38% correspond à la largeur de la section Langages. Le reste de l'écran est découpée en 2 fractions*/
    grid-template-columns: 38% 1fr 1fr;
    grid-template-rows: 60% 20% 20%;
    /*Comment lire le template ci-dessous ? Sur la première ligne (70%) on aura : Languages Experiences Experiences et sur la seconde : OtherSkills OtherSkills Hobbies. On nome les zones et ensuite on va affecter les sections avec la bonne lettre.*/
    grid-template-areas: 
      "L E E"
      "F F F"
      "O O H";

      /*Réglages de base pour les composants de la page*/
    .languagesFrameworks, .experience, .education, .otherSkills, .hobbies {
      background: $color-2;
      border-radius: $borderRadius;
      padding: .8rem;
    }
    h3 {
      font-size: 1.4rem;
      margin-bottom: 1rem;
      font-family: $font-2;
      color: $color-5;
      text-transform: uppercase;
    }

    .languagesFrameworks {
      grid-area: L;
      margin-bottom: .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas: 
        "L"
        "F";

      .languageDisplay {
        grid-area: L;
      }
      .frameworkDisplay {
        grid-area: F;
      }
      h3 {
        margin-bottom: 0.3rem;
      }

      /*Comment faire pour la timeline avec les années et les petites lignes verticales qui descendent*/
      .years {
        font-size: .65rem;
        display: flex;
        justify-content: space-between;
        color: $white;

        span {
          width: 36%;

          /*On va sélectionner le deuxieme span, celui contenant  1an*/
          &:nth-child(2){
            text-align: center; 
            width: 20%;
          }
           /*On va sélectionner le deuxieme span, celui contenant  1an*/
          &:nth-child(3){
            text-align: right;
            width: 44%;
          }
        /*Ici on sélectionne les 2 et on va leur créer un enfant, la petite barre. ATTENTION quand on crée un before ou un after, il faut toujours mettre un content. ici vide. */
          &:nth-child(2):before,
          &:nth-child(3):before {
            content:'';
            background: $white;
            position: absolute;
            height: 146px;
            width: 1px;
            transform: translate(9px, 19px);
          }
        }
      }

      /*Coment faire les jolis barre de progression qui apparaissent avec une animation*/
      .languagesList {
        padding-top: .5rem;

        li {
          color: $color-3;
        }
        .progressBar {
          height: .4rem;
          background: transparent;
          border-radius: 50px;
          margin-top: 0.2rem;
          position: relative;

          /*On crée cet élément enfant qui va remplir l'élément parent dont la width a été calculé*/
          &:after {
            content: '';
            position: absolute;
            background: $color-3;
            border-radius: 50px;
            height: .4rem;
            width: 0;
            animation: line 2s ease-out forwards;

            @keyframes line {
              to {
                width: 100%;
              }
            }
          }
        }
      }
    }
    .experience {
      grid-area: E;
      margin: 0 0 .5rem .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 6% 1fr 1fr;
      grid-template-areas: 
        "1"
        "2"
        "3";
      
      h3 {
        grid-area: 1;
      }
      h4 {
        font-size: 1.1rem;
        color: $color-5;
      }
      h5 {
        color: $color-3;
        padding: 0.5rem 0 0.5rem 1rem;
      }
      ul {
        text-align: justify;
        padding: 0 1rem;
        

        li {
          list-style-type: circle;
          // margin-bottom: 0rem;
          font-size: 0.9rem;

          span {
            font-weight: bold;
          }
        }
      }
      .exp-1 {
        grid-area: 2;
      }
      .exp-2 {
        grid-area: 3;
      }
    }

    .education {
      grid-area: F;
      margin: 0 0 .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 10% 1fr;
      grid-template-areas: 
        "1"
        "2";

        h3 {
          grid-area: 1;
        }

        ul {
          grid-area: 2;
          padding: 1.5rem 0 0 2rem;

          li {
            font-size: 1rem;
            color: $black;
            list-style-type: circle;
          }

        }

    }
    .otherSkills {
      grid-area: O;
      margin-right: 0.5rem;

      .list {
        display: flex;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        padding-right: 5rem;
        justify-content: space-between;

        ul {
          height: 80%;
          display: grid;

          i {
            color: $color-5;
            margin-right: 1rem;
          }
        }
      }
    }
    .hobbies {
      grid-area: H;
      padding-left: 1.5rem;

      ul {
        height: 80%;
        display: grid;
        padding-left: 1rem;

        li {
          i {
            position: absolute;
            color: $color-5;
          }
          span {
            position: absolute;
            transform: translateX(2.2rem);
          }
        }
      }
    }
  }
}
@media screen and (max-width: 950px){
  .knowledgesContent {
    h3 {
      font-size: 1.2rem !important;
    }
    .languagesList {
      padding-top: .2rem !important;
    }
    p {
      font-size: .8rem;
    }
    .otherSkills, .hobbies {
      font-size: .8rem;
    }
  }
}
@media screen and (max-width: 850px){
  .knowledgesContent {
    h4 {
      font-size: .9rem !important;
    }
    h5 {
      font-size: .6rem !important;
      padding: .2rem 1rem !important;
    }
  }
}
@media screen and (max-width: 500px){
  .knowledgesContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 300px 180px 180px !important;
    
    .languages {
      grid-area: 1 !important;
    }
    .experience {
      grid-area: 2 !important;
      margin-left: 0 !important;
    }
    .education {
      grid-area: 3 !important;
      margin-left: 0 !important;
    }
    .otherSkills {
      grid-area: 4 !important;
      margin-right: 0 !important;
    }
    .hobbies {
      grid-area: 5 !important;
      margin-top: .5rem !important;
    }
  }
}